.tabs {
    display: block;
    margin-bottom: 27px;

    &__head {
        display: flex;
        justify-content: center;
        margin-bottom: 32px;

        &-item {
            & + & {
                margin-left: 64px;
            }
        }

        &-link {
            border: 1px solid $color-base;
            border-radius: 24px;
            font-size: 14px;
            line-height: 21px;
            font-weight: 300;
            height: 48px;
            min-width: 152px;
            padding: 3px 16px;
            display: inline-flex;
            align-items: center;
            justify-content: center;

            &:hover {
                background: $color-base;
                color: $color-white;
            }

            &.active {
                background: $color-base;
                color: $color-white;
                font-weight: 800;
            }
        }
    }

    &__body {
        &-item {
            display: none;

            &.open {
                display: block;
            }
        }
    }
}

